    const activeVoters = ref(0);
    const activeTab = ref('active');

    const userBalance = ref(0);
    const votedCount = ref(0);

    const showDaoInfo = ref(false);

    const openDaoInfo = () => {
      showDaoInfo.value = true;
    };

    const closeDaoInfo = () => {
      showDaoInfo.value = false;
    };

    const fetchGroups = async () => {
      try {
        console.log('Fetching groups...');
        const response = await axios.get('/dao/get-groups');
        groups.value = response.data;
      } catch (error) {
        console.error('Error fetching groups:', error);
      }
    };

    const isAuthenticated = computed(() => {
      return store.getters.isAuthenticated;
    });

    const fetchOffers = async () => {
      try {
        console.log('Fetching offers...');
        const response = await axios.get('/dao/get-offers');
        offers.value = response.data;
      } catch (error) {
        console.error('Error fetching offers:', error);
      }
    };

    const fetchUserStats = async () => {
      try {
        console.log('Fetching user stats...');
        if(!store.getters.isAuthenticated) {
          return;
        }
        const response = await axios.get('/dao/get-user-stats');
        votedCount.value = response.data.voted;
        totalUserStaked.value = response.data.totalStaked;

      } catch (error) {
        console.error('Error fetching user stats:', error);
      }
    };

    const fetchDaoStats = async () => {
      try {
        console.log('Fetching user stats...');
        const response = await axios.get('/dao/get-stats');
        activeVoters.value = response.data.activeVoters;
        totalStaked.value = response.data.totalStaked;

      } catch (error) {
        console.error('Error fetching user stats:', error);
      }
    };


    const showCancelModal = ref(false);
    const selectedCancelOffer = ref(null);
    const cancelError = ref('');
    const isCancelling = ref(false);

    const openCancelOfferModal = (offer) => {
      selectedCancelOffer.value = offer;
      showCancelModal.value = true;
    };

    const cancelOffer = async () => {
      if (!selectedCancelOffer.value) return;
      
      isCancelling.value = true;
      cancelError.value = '';

      try {
        await axios.post('/dao/cancel-offer', {
          offerId: selectedCancelOffer.value.offerId
        });
        showCancelModal.value = false;

        serverMessage.value = {
          type: 'success',
          text: 'Offer cancelled successfully!'
        };
        setTimeout(() => {
          serverMessage.value = null;
        }, 5000);

        await fetchOffers();
        await fetchUserStats();
        await fetchDaoStats();
        await fetchUserOtherProposals();
      } catch (error) {
        console.error('Error cancelling offer:', error);
        cancelError.value = error.response?.data?.message || 'Failed to cancel proposal';
      } finally {
        isCancelling.value = false;
      }
    };

    

    const getFilteredOffersForGroup = (groupNameTag) => {
      
      // Проверяем структуру данных предложения
      const groupOffers = offers.value.filter(offer => {
        // Проверяем все возможные пути к nameTag
        const offerGroupNameTag = 
          offer.DaoOfferGroup?.nameTag || 
          offer.groupNameTag || 
          offer.nameTag;
        return offerGroupNameTag === groupNameTag;
      });
      
      
      const userTag = store.getters.userTag;
      
      const filterActiveOffers = (offers) => {
        const filtered = offers.filter(offer => {
          // Считаем предложение активным, если:
          // 1. Статус активный или отсутствует
          // 2. Время окончания не указано или еще не истекло
          const isActive = offer.status === 'active' || !offer.status;
          const end = offer.votingEnd ? new Date(offer.votingEnd) : null;
          const now = new Date();
          const isTimeValid = !end || end > now;
          
          return isActive && isTimeValid;
        });
        return filtered;
      };
      
      const filterCompletedOffers = (offers) => {
        return offers.filter(offer => {
          const isCompleted = offer.status === 'completed';
          if (isCompleted) return true;
          
          const end = offer.votingEnd ? new Date(offer.votingEnd) : null;
          const now = new Date();
          return end && end <= now;
        });
      };

      const filterMyOffers = (offers) => {
        return offers.filter(offer => {
          const isCompleted = offer.status === 'completed';
          if (isCompleted) return false;

          return offer.fromUserTag === userTag;
        });
      };
      
      let filteredOffers;
      switch (activeTab.value) {
        case 'active':
          filteredOffers = filterActiveOffers(groupOffers);
          break;
        case 'completed':
          filteredOffers = filterCompletedOffers(groupOffers);
          break;
        case 'my':
          filteredOffers = filterMyOffers(groupOffers);
          break;
        default:
          filteredOffers = groupOffers;
      }
      
      return filteredOffers;
    };

    const openVoteModal = (offer) => {
      selectedOffer.value = offer;
      voteAmount.value = '';
      voteError.value = '';
      serverMessage.value = null;
      showVoteModal.value = true;
    };

    const formatTimeLeft = (endDate) => {
      const end = new Date(endDate);
      const now = new Date();
      const diff = end - now;

      if (diff <= 0) return 'Voting ended';

      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));

      return `${days}d ${hours}h ${minutes}m left`;
    };

    const formatDate = (date) => {
      if (!date) return '';
      const d = new Date(date);
      return d.toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };


    const formatHistoryDate = (date) => {
      return new Date(date).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }

    const formatUserTag = (userTag) => {
      return userTag || 'Anonymous';
    };

    const canVote = (offer) => {
      // Add your voting eligibility logic here
      return true;
    };

    const canCreateProposal = ref(true); // Add your proposal creation eligibility logic here

    const createProposal = () => {
      showCreateModal.value = true;
      createError.value = '';
      newProposal.value = {
        name: '',
        description: '',
        groupNameTag: ''
      };
    };

    const createProposalInGroup = (groupNameTag) => {
      showCreateModal.value = true;
      createError.value = '';
      newProposal.value = {
        name: '',
        description: '',
        groupNameTag: groupNameTag
      };
    };

    const submitProposal = async () => {
      try {
        isSubmitting.value = true;
        createError.value = '';
        serverMessage.value = null;

        const response = await axios.post('/dao/add-offer', {
          name: newProposal.value.name,
          description: newProposal.value.description,
          groupNameTag: newProposal.value.groupNameTag,
          wallet: store.getters.walletAddress
        });

        if (response.data.offerId) {
          showCreateModal.value = false;
          // Обновляем список предложений
          await fetchOffers();
          await fetchUserStats();
          await fetchDaoStats();

          serverMessage.value = {
            type: 'success',
            text: 'Proposal added successfully!'
          };
          setTimeout(() => {
            serverMessage.value = null;
          }, 5000);
        } else {
          createError.value = response.data.message || 'Failed to create proposal';
        }
      } catch (error) {
        console.error('Error creating proposal:', error);
        createError.value = error.response?.data?.message || 'Failed to create proposal. Try to login.';
      } finally {
        isSubmitting.value = false;
      }
    };

    const getProgress = (start, end) => {
      const now = new Date();
      const diff = end - start;
      const elapsed = now - start;
      return (elapsed / diff) * 100;
    };

    const filteredGroups = computed(() => {
      return groups.value;
    });

    const getActiveCount = computed(() => {
      return offers.value.filter(offer => {
        const isActive = offer.status === 'active' || !offer.status;
        if (!isActive) return false;

        return true;
      }).length;
    });

    const getCompletedCount = computed(() => {
      return offers.value.filter(offer => {
        const isCompleted = offer.status === 'completed';
        if (isCompleted) return true;

        return false;
        
        // const end = offer.votingEnd ? new Date(offer.votingEnd) : null;
        // const now = new Date();
        // return end && end <= now;
      }).length;
    });

    const getMyCount = computed(() => {
      const userTag = store.getters.userTag;
      return offers.value.filter(offer => offer.fromUserTag === userTag).length;
    });

    const emptyStateMessage = computed(() => {
      switch (activeTab.value) {
        case 'active':
          return 'No Active Proposals';
        case 'completed':
          return 'No Completed Proposals';
        case 'my':
          return 'No Personal Proposals';
        default:
          return 'No Proposals Found';
      }
    });

    const emptyStateDescription = computed(() => {
      switch (activeTab.value) {
        case 'active':
          return 'There are no active proposals at the moment. Create one to get started!';
        case 'completed':
          return 'No proposals have been completed yet. Check back later!';
        case 'my':
          return 'You haven\'t created any proposals yet. Start participating in the DAO!';
        default:
          return 'Start participating in the DAO by creating or voting on proposals.';
      }
    });

    const getVotingPercentage = computed(() => {
      if (!userBalance.value || userBalance.value === 0) return 100;
      
      const diff = userBalance.value - totalUserStaked.value;
      const percentage = (diff / userBalance.value) * 100;
      
      return Math.round(percentage);
    });

    const showCreateModal = ref(false);
    const createError = ref('');
    const isSubmitting = ref(false);
    const newProposal = ref({
      name: '',
      description: '',
      groupNameTag: ''
    });

    const fetchUserBalance = async () => {
      console.log('Fetching user balance...');
      try {
        const wallet = store.getters.walletAddress;
        console.log('Current wallet:', wallet);
        
        if(!wallet) {
          return;
        }
        
        const response = await axios.get(`/dapp/get-bitcat-balance/${wallet}`);
        userBalance.value = response.data.balance || 0;
      } catch (error) {
        console.error('Error fetching user balance:', error);
      }
    };

    const userVotes = ref([]);
    const fetchUserVotes = async () => {
      if(!store.getters.isAuthenticated) {
        return;
      }

      try {
        const response = await axios.get('/dao/get-user-votes/');
        userVotes.value = response.data;
      } catch (error) {
        console.error('Error fetching user votes:', error);
        serverMessage.value = {
          type: 'error',
          text: 'Failed to fetch user votes'
        };
        setTimeout(() => {
          serverMessage.value = null;
        }, 5000);
      }
    };

    const cancelVote = async (voteId) => {
      try {
        await axios.post('/dao/cancel-user-vote', { voteId });
        await fetchUserVotes(); // Refresh votes after cancellation
        await fetchOffers();
        await fetchDaoStats();
        await fetchUserStats();
        serverMessage.value = {
          type: 'success',
          text: 'Vote cancelled successfully'
        };

        setTimeout(() => {
          serverMessage.value = null;
        }, 5000);
      } catch (error) {
        console.error('Error cancelling vote:', error);
        serverMessage.value = {
          type: 'error',
          text: 'Failed to cancel vote'
        };
        setTimeout(() => {
          serverMessage.value = null;
        }, 5000);
      }
    };

    const showVoteModal = ref(false);
    const showFullInfo = ref(false);
    const voteError = ref('');
    const isVoting = ref(false);
    const voteAmount = ref('');
    const presetAmounts = ref([100, 1000, 10000, 1000000]);
    const selectedOffer = ref(null);
    const showVoteInfo = ref(false);
    const serverMessage = ref(null);

    const formatAmount = (amount) => {
      if (amount >= 1000000) {
        return amount/1000000 + 'M';
      } else if (amount >= 1000) {
        return amount/1000 + 'K';
      }
      return amount.toString();
    };

    const selectAmount = (amount) => {
      voteAmount.value = amount;
    };

    const submitVote = async () => {
      try {
        isVoting.value = true;
        voteError.value = '';
        serverMessage.value = null;

        const response = await axios.post('/dao/add-vote', {
          offerId: selectedOffer.value.offerId,
          vote: Number(voteAmount.value),
          wallet: store.getters.walletAddress
        });

        if (response.data.voteId) {
          showVoteModal.value = false;
          // Обновляем список предложений
          await fetchOffers();
          await fetchUserStats();
          await fetchDaoStats();
          await fetchUserVotes();
          await fetchVotingHistory();
          serverMessage.value = {
            type: 'success',
            text: 'Vote submitted successfully!'
          };
          setTimeout(() => {
            serverMessage.value = null;
          }, 5000);
        } else {
          voteError.value = response.data.message || 'Failed to submit vote';
        }
      } catch (error) {
        console.error('Error submitting vote:', error);
        voteError.value = error.response?.data?.message || 'Failed to submit vote. Try to login.';
      } finally {
        isVoting.value = false;
      }
    };

    const errorMessage = ref('');

    const storeWalletAddress = computed(() => store.getters.walletAddress)
    watch(storeWalletAddress, async () => {
      await fetchUserBalance();
    })


    const votingHistory = ref([]);
    const currentHistoryPage = ref(1);
    
    const fetchVotingHistory = async (page = 1) => {
      try {
        const response = await axios.get(`/dao/get-votes-history?page=${page}`);
        votingHistory.value = response.data;
      } catch (error) {
        console.error('Error fetching voting history:', error);
        serverMessage.value = {
          type: 'error',
          text: 'Failed to fetch voting history'
        };
        setTimeout(() => {
          serverMessage.value = null;
        }, 5000);
      }
    };
    
    const nextHistoryPage = async () => {
      currentHistoryPage.value++;
      await fetchVotingHistory(currentHistoryPage.value);
    };
    
    const prevHistoryPage = async () => {
      if (currentHistoryPage.value > 1) {
        currentHistoryPage.value--;
        await fetchVotingHistory(currentHistoryPage.value);
      }
    };

    const userOtherProposals = ref([]);

    const fetchUserOtherProposals = async () => {
      try {
        if(!store.getters.isAuthenticated) {
          return;
        }
        const response = await axios.get('/dao/get-user-offers/');
        userOtherProposals.value = response.data;
      } catch (error) {
        console.error('Error fetching other proposals:', error);
      }
    };

    const formatStakedAmount = computed(() => {
      return (value) => {
        if (!value) return '0';
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      };
    });

    onMounted(async () => {
      await fetchUserBalance();
      fetchGroups();
      fetchOffers();
      fetchUserStats();
      fetchDaoStats();
      fetchUserVotes();
      fetchVotingHistory();
      fetchUserOtherProposals();
    });
    

    return {
      formatStakedAmount,
      showDaoInfo,
      openDaoInfo,
      closeDaoInfo,
      userOtherProposals,
      votingHistory,
      currentHistoryPage,
      nextHistoryPage,
      prevHistoryPage,
      isAuthenticated,
      showCancelModal,
      cancelError,
      isCancelling,
      openCancelOfferModal,
      cancelOffer,
      groups,
      offers,
      totalStaked,
      totalUserStaked,
      activeVoters,
      getFilteredOffersForGroup,
      openVoteModal,
      formatTimeLeft,
      formatDate,
      formatUserTag,
      canVote,
      canCreateProposal,
      createProposal,
      createProposalInGroup,
      getProgress,
      activeTab,
      filteredGroups,
      getActiveCount,
      getCompletedCount,
      getMyCount,
      emptyStateMessage,
      emptyStateDescription,
      showCreateModal,
      createError,
      isSubmitting,
      newProposal,
      submitProposal,
      userBalance,
      votedCount,
      showVoteModal,
      showFullInfo,
      voteError,
      isVoting,
      voteAmount,
      presetAmounts,
      selectedOffer,
      formatAmount,
      selectAmount,
      submitVote,
      showVoteInfo,
      serverMessage,
      errorMessage,
      getVotingPercentage,
      userVotes,
      cancelVote,
      formatHistoryDate
    };
  }
};
</script>

